.slider-actions__arrows .swiper-button-next,
.slider-actions__arrows .swiper-button-prev {
  position: relative;
  width: 50%;
  height: 100%;
  margin-top: 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: 200ms ease;
  border: 2px solid rgba(0, 64, 119, 0.2);
}
.slider-actions__arrows .swiper-button-next.swiper-button-disabled,
.slider-actions__arrows .swiper-button-prev.swiper-button-disabled {
  opacity: 1;
  pointer-events: all;
}
.slider-actions__arrows .swiper-button-next:hover,
.slider-actions__arrows .swiper-button-prev:hover {
  background-color: var(--color-blue);
}
.slider-actions__arrows .swiper-button-next:hover::after,
.slider-actions__arrows .swiper-button-prev:hover::after {
  background-color: var(--color-white);
}
.slider-actions__arrows .swiper-button-next::after,
.slider-actions__arrows .swiper-button-prev::after {
  position: absolute;
  content: "";
  mask: url("../img/chevron.svg") no-repeat center/contain;
  width: 45%;
  height: 45%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--color-black);
  transition: 200ms ease;
}
.slider-actions__arrows .swiper-button-prev {
  border-right: none;
  border-radius: 8px 0 0 8px;
}
.slider-actions__arrows .swiper-button-next {
  border-radius: 0 8px 8px 0;
  border-left: none;
}
