.dropmenu {
  position: absolute;
  z-index: 10;
  top: 45px;
  left: 0;
  width: 376px;
  padding: 32px;
  border-radius: 8px;
  background-color: var(--color-white);
  border: 1px solid rgba(0, 64, 119, 0.2);
  transition: 450ms ease;
