.checkbox {
  position: relative;
  width: 100%;
  height: 100%;
  cursor: pointer;
  display: flex;
  align-items: center;
}
.checkbox__box {
  display: inline-block;
  width: 16px;
  height: 16px;
  position: relative;
  margin-right: 8px;
  border: 1px solid rgba(0, 64, 119, 0.2);
  border-radius: 4px;
}
.checkbox__input {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
.checkbox__input:checked + .checkbox__box {
  border: none;
}
.checkbox__input:checked + .checkbox__box::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-image: url("../img/check-full.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
