.hometowns-topics {
  @include adaptive-value("padding-top", 60, 40, 1);
  @include adaptive-value("padding-bottom", 60, 40, 1);

  &__title {
    @include adaptive-value("margin-bottom", 60, 40, 1);
    @include fz48();
  }

  &__slide {
    width: auto;
    box-sizing: border-box;
  }
}

.topics-item {
  border: 2px solid #ccebff;
  border-radius: 15px;
  background-color: #f2f5f7;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  @include adaptive-value("padding", 30, 20, 1);
  @include adaptive-value("padding-top", 30, 30, 1);
  @include adaptive-value("padding-bottom", 30, 20, 1);
  @include adaptive-value("padding-left", 20, 20, 1);
  @include adaptive-value("width", 245, 200, 1);
  @include adaptive-value("height", 205, 180, 1);

  &__top {
    font-size: 16px;
    line-height: 1;
    font-weight: 700;
    text-transform: uppercase;
    color: #0f0f0f;
    margin-bottom: 25px;
    position: relative;
    display: flex;
    align-items: center;

    span {
      width: 30px;
      height: 30px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: var(--color-blue-light);
      margin-right: 15px;

      svg {
        width: 14px;
        height: 14px;

        path {
          fill: var(--color-white);
        }
      }
    }
  }

  &__text {
    @include adaptive-value("font-size", 16, 14, 1);
  }
}
